.section-content
    position: relative
    left:120px
    height: 100%
    background: transparent
    z-index: -1

.section-background
    width: 100%
    height: 100%
    position: absolute
    top:0%
    z-index: -100

.section-background video
    object-fit: cover
    position: relative
    height: 100%
    z-index: -1000

.section-content-card
    position: relative
    color: white
    font-size: 5rem 
    top:35%
    width: fit-content
    height:300px
    padding: 2rem